import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  data_posts_school,
  data_school,
} from "../containers/AccessManagement/dataTest";
import { PostSchoolInterface, SchoolInfoInterface } from "../shared/interfaces";
import { setAxiosToken } from "../utils/AxiosToken";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface FetchSchoolsAction {
  type: ActionTypes.FETCH_SCHOOLS;
  payload: SchoolInfoInterface[];
}

export interface CreateSchoolAction {
  type: ActionTypes.CREATE_SCHOOL;
  payload: SchoolInfoInterface;
}

export interface UpdateSchoolAction {
  type: ActionTypes.UPDATE_SCHOOL;
  payload: SchoolInfoInterface;
}

export interface DeleteSchoolAction {
  type: ActionTypes.DELETE_SCHOOL;
  payload: SchoolInfoInterface[];
}

// School Posts interfaces
export interface FetchSchoolPostsAction {
  type: ActionTypes.FETCH_SCHOOL_POSTS;
  payload: PostSchoolInterface[];
}

export interface CreateSchoolPostAction {
  type: ActionTypes.CREATE_SCHOOL_POST;
  payload: PostSchoolInterface;
}

export interface RespondRequestInterface {
  post_at_school_id: string;
  approved_posts: number;
  approved_explanation: string;
  approved_date: Date;
  post_at_school_status: number;
  confirmed_by: string;
}
export interface UpdateSchoolPostAction {
  type: ActionTypes.UPDATE_SCHOOL_POST;
  payload: RespondRequestInterface;
}

export interface DeleteSchoolPostAction {
  type: ActionTypes.DELETE_SCHOOL_POST;
  payload: PostSchoolInterface[];
}

export type SchoolActionType =
  | FetchSchoolsAction
  | CreateSchoolAction
  | UpdateSchoolAction
  | DeleteSchoolAction
  | FetchSchoolPostsAction
  | CreateSchoolPostAction
  | UpdateSchoolPostAction
  | DeleteSchoolPostAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const fetchSchools = () => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();
    // const response = await axios.get<AccessRoleType[]>(url);

    dispatch<FetchSchoolsAction>({
      type: ActionTypes.FETCH_SCHOOLS,
      payload: data_school,
      // payload: response.data,
    });
  };
};

export const createSchool = (data: SchoolInfoInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CreateSchoolAction>({
      type: ActionTypes.CREATE_SCHOOL,
      payload: data,
    });
  };
};

export const updateSchool = (data: SchoolInfoInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<UpdateSchoolAction>({
      type: ActionTypes.UPDATE_SCHOOL,
      payload: data,
    });
  };
};

export const deleteSchool = (data: SchoolInfoInterface[]) => {
  return async (dispatch: Dispatch) => {
    dispatch<DeleteSchoolAction>({
      type: ActionTypes.DELETE_SCHOOL,
      payload: data,
    });
  };
};

export const fetchSchoolPosts = () => {
  return async (dispatch: Dispatch) => {
    // const response = await axios.get<AccessRoleType[]>(url);

    dispatch<FetchSchoolPostsAction>({
      type: ActionTypes.FETCH_SCHOOL_POSTS,
      payload: data_posts_school,
      // payload: response.data,
    });
  };
};

export const createSchoolPost = (data: PostSchoolInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CreateSchoolPostAction>({
      type: ActionTypes.CREATE_SCHOOL_POST,
      payload: data,
    });
  };
};

export const updateSchoolPost = (data: RespondRequestInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<UpdateSchoolPostAction>({
      type: ActionTypes.UPDATE_SCHOOL_POST,
      payload: data,
    });
  };
};

export const deleteSchoolPost = (data: PostSchoolInterface[]) => {
  return async (dispatch: Dispatch) => {
    dispatch<DeleteSchoolPostAction>({
      type: ActionTypes.DELETE_SCHOOL_POST,
      payload: data,
    });
  };
};
