import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface GetDocumentCategoryAction {
  type: ActionTypes.GET_DOCUMENT_CATEGORY;
  payload: DocumentCategoryInterface[];
}

export interface CreateDocumentCategoryAction {
  type: ActionTypes.CREATE_DOCUMENT_CATEGORY;
  payload: DocumentCategoryInterface;
}

export interface UpdateDocumentCategoryAction {
  type: ActionTypes.UPDATE_DOCUMENT_CATEGORY;
  payload: DocumentCategoryInterface;
}

export interface DeleteDocumentCategoryAction {
  type: ActionTypes.DELETE_DOCUMENT_CATEGORY;
  payload: string;
}

export interface GetDocumentSubCategoryAction {
  type: ActionTypes.GET_DOCUMENT_SUB_CATEGORY;
  payload: DocumentSubCategoryInterface[];
}

export interface CreateDocumentSubCategoryAction {
  type: ActionTypes.CREATE_DOCUMENT_SUB_CATEGORY;
  payload: DocumentSubCategoryInterface;
}

export interface UpdateDocumentSubCategoryAction {
  type: ActionTypes.UPDATE_DOCUMENT_SUB_CATEGORY;
  payload: DocumentSubCategoryInterface;
}

export interface DeleteDocumentSubCategoryAction {
  type: ActionTypes.DELETE_DOCUMENT_SUB_CATEGORY;
  payload: string;
}

export type DocumentCategoryActionType =
  | GetDocumentCategoryAction
  | CreateDocumentCategoryAction
  | UpdateDocumentCategoryAction
  | DeleteDocumentCategoryAction
  | GetDocumentSubCategoryAction
  | CreateDocumentSubCategoryAction
  | UpdateDocumentSubCategoryAction
  | DeleteDocumentSubCategoryAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const getDocumentCategory = () => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<DocumentCategoryInterface[]>(
        `${API.AUTH}/detailcategory`
      );
      await dispatch<GetDocumentCategoryAction>({
        type: ActionTypes.GET_DOCUMENT_CATEGORY,
        payload: res.data,
      });
    } catch (error) {
      console.log({ doc_category_error: error });
    }
  };
};

export const createDocumentCategory = (data: DocumentCategoryInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CreateDocumentCategoryAction>({
      type: ActionTypes.CREATE_DOCUMENT_CATEGORY,
      payload: data,
    });
  };
};

export const updateDocumentCategory = (data: DocumentCategoryInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<UpdateDocumentCategoryAction>({
      type: ActionTypes.UPDATE_DOCUMENT_CATEGORY,
      payload: data,
    });
  };
};

export const deleteDocumentCategory = (data: string) => {
  return async (dispatch: Dispatch) => {
    dispatch<DeleteDocumentCategoryAction>({
      type: ActionTypes.DELETE_DOCUMENT_CATEGORY,
      payload: data,
    });
  };
};

