import axios from "axios";
import React, { useEffect, useState } from "react";
import {
  MdAdd,
  MdClear,
  MdClose,
  MdCreate,
  MdErrorOutline,
  MdModeEdit,
  MdSettings,
  MdViewList,
  MdVisibility,
  MdVisibilityOff,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
} from "../../actions/document";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  DocumentCategoryInterface,
  DocumentFieldInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import CreateDocument from "./CreateDocument";
import CreateSubDocument from "./CreateSubDocument";
import SubCategory from "./SubCategory";
interface AppProps {
  document: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  deleteDocumentCategory: Function;
}
const Index: React.FC<AppProps> = ({
  document,
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
}) => {
  const [categoryId, setCategoryId] = useState<string>("");
  const [categoryTitle, setCategoryTitle] = useState<string>("");
  const [categoryDescription, setCategoryDescription] = useState<string>("");
  const [categoryFields, setCategoryFields] = useState<
    DocumentFieldInterface[]
  >([]);
  const [subCategoryId, setSubCategoryId] = useState<string>("");
  const [subCategoryTitle, setSubCategoryTitle] = useState<string>("");
  const [subCategoryDescription, setSubCategoryDescription] =
    useState<string>("");
  const [actionType, setActionType] = useState<
    "CREATE_SUB" | "CREATE_CAT" | "UPDATE_SUB" | "UPDATE_CAT" | ""
  >("");
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>();
  const [loading, setLoadind] = useState<boolean>(false);
  const [loadingPage, setLoadindPage] = useState<boolean>(true);

  useEffect(() => {
    if (!document.documentCategory.length) {
      getDocumentCategory();
    } else {
      setLoadindPage(false);
    }
  }, [document.documentCategory.length, getDocumentCategory]);

  useEffect(() => {
    if (!document.documentSubCategory.length) {
      getDocumentSubCategory();
    }
  });

  const handleDelete = async (categoryId: string) => {
    setFeedBack({ type: "", message: "" });
    setLoadind(true);
    try {
      const res = await axios.delete<DocumentCategoryInterface>(
        `${API.AUTH}/detailcategory/${categoryId}`
      );
      if (res.data) {
        deleteDocumentCategory(categoryId);
        setFeedBack({
          type: "success",
          message: `Category has been deleted successfully.`,
        });
        setLoadind(false);
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      setLoadind(false);
    }
  };

  return (
    <div className="mx-28">
      <div className="bg-hero mt-3 container rounded p-5 flex flex-row justify-between items-center">
        <div className="flex flex-col">
          <span className="text-blue-100 text-4xl font-light">
            Document Categories
          </span>
          {/* <span className="text-white text-lg font-bold">
            Course: 1212323-Mathematics for engineers
          </span> */}
        </div>
        {/* <div className="flex flex-row"> */}
        {/* <Link
            to="/method-add-question"
            className="btn bg-black bg-opacity-30 text-white border-2 border-white mr-1"
          >
            Add questions
          </Link> */}
        {/* <div className="btn bg-black bg-opacity-30 text-white border-2 border-white">
            Change course
          </div>
        </div> */}
      </div>
      {loadingPage ? (
        <div className="text-center py-5 text-2xl">Loading...</div>
      ) : (
        <div className="container mx-auto my-4">
          {feedBack && actionType === "" && feedBack.type !== "" && (
            <div
              className={`alert-${feedBack.type} flex flex-row items-center relative mt-2`}
            >
              <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
                <MdClose
                  className="h-5 w-5 text-gray-700 hover:font-bold"
                  title="Close"
                  onClick={() => setFeedBack({ type: "", message: "" })}
                />
              </span>
              <MdErrorOutline className="h-10 w-10" />
              <div className="alert-content ml-4">
                <div className="alert-title font-semibold text-lg">
                  {feedBack.type === "success" ? "Success" : "Error"}
                </div>
                <div className="alert-description text-sm">
                  {feedBack.message}
                </div>
              </div>
            </div>
          )}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            {/* <!-- Left Side --> */}
            <div className="flex flex-col items-center gap-5">
              <div className="w-full bg-white p-3 border-t-2 border-gray-400">
                <div className="border-b flex justify-between">
                  <div className="text-black text-3xl font-medium p-2 flex items-center gap-1">
                    <MdViewList className="w-9 h-9" /> Categories
                  </div>
                  <button
                    type="button"
                    className="bg-gray-300 text-gray-700 px-4 py-2 duration-500 ease select-none hover:bg-gray-400 hover:text-black focus:outline-none focus:shadow-outline"
                    onClick={() => setActionType("CREATE_CAT")}
                    disabled={
                      actionType === "CREATE_CAT" || actionType === "UPDATE_CAT"
                    }
                  >
                    + Category
                  </button>
                </div>

                <section className="flex flex-col bg-gray-50 h-full overflow-auto">
                  <label className="px-3 mt-3">
                    <input
                      className="rounded-lg p-4 bg-gray-100 transition duration-200 focus:outline-none focus:ring-2 w-full"
                      placeholder="Search..."
                    />
                  </label>

                  <ul className="mt-3">
                    {document.documentCategory.length &&
                      document.documentCategory.map((category, i) => {
                        return (
                          <li
                            key={i}
                            className={`py-5 border-b px-3 transition ${
                              category.user_detail_category_id === categoryId
                                ? "bg-blue-500 text-white"
                                : "hover:bg-blue-100"
                            }`}
                          >
                            <div className="flex justify-between items-center">
                              <h3 className="text-lg font-semibold">
                                {category.title}
                              </h3>
                              <div>
                                <div className="flex items-center gap-2">
                                  {category.user_detail_category_id ===
                                  categoryId ? (
                                    <button
                                      className="bg-gray-300 p-2 rounded-full shadow-md focus:outline-none text-black flex justify-center items-center"
                                      onClick={() => {
                                        setCategoryId("");
                                        setCategoryTitle("");
                                        setFeedBack({ type: "", message: "" });
                                      }}
                                    >
                                      {/* Sub Categories */}
                                      <MdVisibilityOff className="w-4 h-4" />
                                    </button>
                                  ) : (
                                    <button
                                      className="bg-gray-300 p-2 rounded-full shadow-md focus:outline-none flex justify-center items-center"
                                      onClick={() => {
                                        setCategoryId(
                                          category.user_detail_category_id
                                        );
                                        setCategoryTitle(category.title);
                                        setFeedBack({ type: "", message: "" });
                                      }}
                                    >
                                      {/* Sub Categories */}
                                      <MdVisibility className="w-4 h-4" />
                                    </button>
                                  )}
                                  <button
                                    className="bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center"
                                    onClick={() => {
                                      setCategoryId(
                                        category.user_detail_category_id
                                      );
                                      setCategoryTitle(category.title);
                                      setCategoryDescription(
                                        category.description
                                      );
                                      setCategoryFields(category.fields);
                                      setActionType("UPDATE_CAT");
                                    }}
                                  >
                                    <MdCreate className="text-white w-4 h-4" />
                                  </button>
                                  <button
                                    className="bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center"
                                    onClick={() =>
                                      handleDelete(
                                        category.user_detail_category_id
                                      )
                                    }
                                    disabled={loading}
                                  >
                                    <MdClear className="text-white w-4 h-4" />
                                  </button>
                                </div>
                              </div>
                            </div>
                            <div
                              className={`text-md italic ${
                                category.user_detail_category_id === categoryId
                                  ? "text-gray-100"
                                  : "text-gray-400"
                              }`}
                            >
                              {category.description}
                            </div>
                          </li>
                        );
                      })}
                  </ul>
                </section>
              </div>
              {/* <!-- End of Verified List card --> */}
            </div>

            {/* <!-- Right Side --> */}
            <div className="max-h-full bg-white p-3 shadow-sm rounded-sm overflow-auto">
              {categoryId ? (
                <SubCategory
                  categoryId={categoryId}
                  actionType={actionType}
                  categoryTitle={categoryTitle}
                  setSubCategoryId={setSubCategoryId}
                  setSubCategoryTitle={setSubCategoryTitle}
                  setSubCategoryDescription={setSubCategoryDescription}
                  setActionType={setActionType}
                  feedBack={feedBack}
                  setFeedBack={setFeedBack}
                  documentSubCategory={document.documentSubCategory}
                />
              ) : (
                <div className="flex flex-col items-center self-center mt-12">
                  <MdSettings className="w-16 h-16" />
                  <p>Select Category to View Sub Categories</p>
                </div>
              )}
            </div>
            {/* <!-- End of Right Side --> */}
          </div>
        </div>
      )}

      {/* modal for creating new categories or sub category */}
      {actionType !== "" && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {}}
          displayClose={false}
          backDropClose={false}
          title={
            <div className="flex items-center space-x-5">
              <div className="h-14 w-14 bg-blue-200 rounded-full flex flex-shrink-0 justify-center items-center text-blue-500">
                {actionType === "CREATE_SUB" || "CREATE_CAT" ? (
                  <MdAdd className="w-10 h-10" />
                ) : (
                  <MdModeEdit className="w-10 h-10" />
                )}
              </div>
              <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                <p className="text-base text-gray-500 font-medium">
                  You are{" "}
                  {actionType === "CREATE_SUB" || "CREATE_CAT"
                    ? "creating"
                    : "updating"}
                </p>
                <h2 className="uppercase text-blue-600">
                  {actionType === "CREATE_SUB"
                    ? "New Sub "
                    : actionType === "CREATE_CAT"
                    ? "New "
                    : actionType === "UPDATE_SUB" && "Sub "}{" "}
                  Category
                </h2>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="max-w-md mx-auto mb-8">
            {(actionType === "CREATE_SUB" || actionType === "UPDATE_SUB") && (
              <CreateSubDocument
                categoryId={categoryId}
                setCategoryTitle={setCategoryTitle}
                categoryTitle={categoryTitle}
                subCategoryId={subCategoryId}
                setSubCategoryId={setSubCategoryId}
                setSubCategoryTitle={setSubCategoryTitle}
                subCategoryTitle={subCategoryTitle}
                setSubCategoryDescription={setSubCategoryDescription}
                subCategoryDescription={subCategoryDescription}
                setActionType={setActionType}
                actionType={actionType}
                feedBack={feedBack}
                setFeedBack={setFeedBack}
              />
            )}
            {(actionType === "CREATE_CAT" || actionType === "UPDATE_CAT") && (
              <CreateDocument
                setCategoryId={setCategoryId}
                categoryId={categoryId}
                setCategoryTitle={setCategoryTitle}
                categoryTitle={categoryTitle}
                setCategoryDescription={setCategoryDescription}
                categoryDescription={categoryDescription}
                categoryFields={categoryFields}
                setCategoryFields={setCategoryFields}
                setActionType={setActionType}
                actionType={actionType}
                feedBack={feedBack}
                setFeedBack={setFeedBack}
              />
            )}
          </div>
        </Modal>
      )}
      {/* end modal for creating new categories or sub category */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return { document };
};
export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
})(Index);
