import React, { useState } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import {
  MdCancel,
  MdKeyboardArrowLeft,
  MdKeyboardArrowRight,
} from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import { UserDocumentsInterface } from "../../shared/interfaces";

interface FooterResponsePropInterface {
  docTitle: string;
  docStatus: "waiting" | "approved" | "rejected";
  setActionTaken: React.Dispatch<
    React.SetStateAction<"" | "approval" | "rejection">
  >;
  actionTaken: "" | "approval" | "rejection";
  responseMsg: string;
  loadingRequest: boolean;
  setResponseMsg: React.Dispatch<React.SetStateAction<string>>;
  handleVerify: () => Promise<void>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
  error: {
    element: string;
    msg: string;
  };
}

const FooterResponse: React.FC<FooterResponsePropInterface> = ({
  docTitle,
  docStatus,
  setActionTaken,
  actionTaken,
  responseMsg,
  loadingRequest,
  setResponseMsg,
  handleVerify,
  error,
  setError,
}): JSX.Element => {
  return (
    <div className="w-full bg-gray-300 flex md:flex-row flex-col justify-between items-center px-5 pt-4 pb-8 gap-1">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section>
          <h2 className="text-blue-600 font-semibold text-xl">{docTitle}</h2>
          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      <div className="flex flex-col md:flex-row items-center gap-4">
        <div>
          <textarea
            placeholder={`Comment for the ${actionTaken}`}
            cols={50}
            rows={3}
            className={`border border-${
              error.element === "comment" ? "red" : "gray"
            }-400 rounded px-3 py-1 w-full md:w-auto focus:outline-none`}
            style={{ resize: "none" }}
            value={responseMsg}
            onChange={(e) => {
              setResponseMsg(e.target.value);
              setError({ element: "", msg: "" });
            }}
          ></textarea>
          {error.element === "comment" && (
            <p className="text-red-800">{error.msg}</p>
          )}
        </div>
        <div className="flex flex-col gap-1">
          <button
            className="bg-gray-800 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => {
              setResponseMsg("");
              setActionTaken("");
              setError({ element: "", msg: "" });
            }}
            disabled={loadingRequest}
          >
            <RiArrowGoBackFill className="w-5 h-5" />
            <span>Back</span>
          </button>
          <button
            className="bg-blue-700 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 gap-1 focus:outline-none"
            disabled={loadingRequest}
            onClick={() => handleVerify()}
          >
            <FaRegCheckCircle className="w-5 h-5" />
            <span>Confirm</span>
          </button>
        </div>
      </div>
      {/* end right part */}
    </div>
  );
};
interface FooterInitialPropInterface {
  docTitle: string;
  docStatus: "waiting" | "approved" | "rejected";
  setActionTaken: React.Dispatch<
    React.SetStateAction<"" | "approval" | "rejection">
  >;
  setResponse: React.Dispatch<React.SetStateAction<"approved" | "rejected">>;
}

const FooterInitial: React.FC<FooterInitialPropInterface> = ({
  docTitle,
  docStatus,
  setActionTaken,
  setResponse,
}): JSX.Element => {
  return (
    <div className="w-full bg-gray-300 flex md:flex-row flex-col justify-between items-center px-5 pt-4 pb-8 gap-1">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* buttons next & prev */}
        <div className="flex items-center gap-1">
          <button className="bg-white text-gray-600 focus:outline-none">
            <MdKeyboardArrowLeft className="w-12 h-12" />
          </button>
          <button className="bg-white text-blue-600 focus:outline-none">
            <MdKeyboardArrowRight className="w-12 h-12" />
          </button>
        </div>
        {/* end buttons next & prev */}
        {/* doc title & status */}
        <section>
          <h2 className="text-blue-600 font-semibold text-xl">{docTitle}</h2>
          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      {docStatus === "waiting" && (
        <div className="flex flex-col md:flex-row items-center gap-4">
          <button
            className="bg-red-800 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => {
              setActionTaken("rejection");
              setResponse("rejected");
            }}
          >
            <MdCancel className="w-5 h-5" />
            <span>Reject</span>
          </button>
          <button
            className="bg-blue-700 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
            onClick={() => {
              setActionTaken("approval");
              setResponse("approved");
            }}
          >
            <FaRegCheckCircle className="w-5 h-5" />
            <span>Approve</span>
          </button>
        </div>
      )}
      {/* end right part */}
    </div>
  );
};

interface AppProps {
  doc: UserDocumentsInterface;
  docTitle: string;
  responseMsg: string;
  setResponseMsg: React.Dispatch<React.SetStateAction<string>>;
  loadingRequest: boolean;
  setResponse: React.Dispatch<React.SetStateAction<"approved" | "rejected">>;
  handleVerify: () => Promise<void>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
  error: {
    element: string;
    msg: string;
  };
}
const FileView: React.FC<AppProps> = ({
  doc,
  docTitle,
  setResponseMsg,
  responseMsg,
  loadingRequest,
  setResponse,
  handleVerify,
  setError,
  error,
}) => {
  const [actionTaken, setActionTaken] = useState<"" | "approval" | "rejection">(
    ""
  );
  return (
    <div className="bg-gray-50">
      {/* User Access Select */}
      {/* image content */}
      <img
        src={`http://165.22.77.142/api/v1/files/${doc.files}`}
        alt=""
        className="w-full"
      />
      {/* end image content */}

      {/* footer */}
      {!actionTaken ? (
        <FooterInitial
          docTitle={docTitle}
          docStatus={doc.validation_status}
          setActionTaken={setActionTaken}
          setResponse={setResponse}
        />
      ) : (
        <FooterResponse
          docTitle={docTitle}
          docStatus={doc.validation_status}
          setActionTaken={setActionTaken}
          actionTaken={actionTaken}
          setResponseMsg={setResponseMsg}
          responseMsg={responseMsg}
          loadingRequest={loadingRequest}
          handleVerify={handleVerify}
          error={error}
          setError={setError}
        />
      )}
      {/* end footer */}
    </div>
  );
};

export default FileView;
