import { ActionTypes } from "../actions";
import { SchoolActionType } from "../actions/schools";
import { PostSchoolInterface, SchoolInfoInterface } from "../shared/interfaces";

export interface SchoolReducerType {
  schools: SchoolInfoInterface[];
  schoolPosts: PostSchoolInterface[];
}

// default state
const initialState = {
  schools: [],
  schoolPosts: [],
};

export const schoolReducer = (
  state: SchoolReducerType = initialState,
  action: SchoolActionType
) => {
  let index;
  switch (action.type) {
    case ActionTypes.FETCH_SCHOOLS:
      return {
        ...state,
        schools: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL:
      return {
        ...state,
        schools: [...state.schools, action.payload],
      };
    case ActionTypes.FETCH_SCHOOL_POSTS:
      return {
        ...state,
        schoolPosts: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL_POST:
      return {
        ...state,
        schoolPosts: [...state.schoolPosts, action.payload],
      };
    case ActionTypes.UPDATE_SCHOOL_POST:
      index = state.schoolPosts.findIndex(
        (post) => post.post_at_school_id === action.payload.post_at_school_id
      ); //finding index of the item
      if (index >= 0) {
        state.schoolPosts[index].confirmed_posts =
          action.payload.approved_posts;
        state.schoolPosts[index].approval_date = action.payload.approved_date;
        state.schoolPosts[index].post_at_school_status =
          action.payload.post_at_school_status;
        state.schoolPosts[index].confirmed_posts =
          action.payload.approved_posts;
        state.schoolPosts[index].confirmed_by = action.payload.confirmed_by;
      }
      return state;
    default:
      return state;
  }
};
