// Multiple question interface
export interface question_answer {
  answer: string;
  status: boolean;
}
export interface psychometric_question {
  question_id: string;
  question_title: string;
  question_description: string;
  question_answers: question_answer[]
}

export enum questionTypeElementsType {
  MultipleChoice = "MultipleChoice",
  TrueFalse = "TrueFalse",
  Matching = "Matching",
  Numeric = "Numeric",
}

export const questionTypeElements: { key: questionTypeElementsType; value: string; }[] = [
  {
    key: questionTypeElementsType.MultipleChoice,
    value: "Multiple choice Question",
  },
  {
    key: questionTypeElementsType.TrueFalse,
    value: "True & False Question",
  },
  {
    key: questionTypeElementsType.Matching,
    value: "Matching Question",
  },
  {
    key: questionTypeElementsType.Numeric,
    value: "Numeric Question",
  },
];

export interface questionType {
  status: number;
  title: string;
  element: questionTypeElementsType;
  description: string;
  createdAt?: string;
  updatedAt?: string;
  id?: string;
}