import { Component } from 'react'
import { Fragment } from 'react';
import { IoIosBriefcase } from 'react-icons/io';
import Modal, { ModalSize, Themes } from '../../../components/Modal/Modal';
import SchoolLevelsFilter from './SchoolLevelsFilter/SchoolLevelsFilter';
import * as Interfaces from '../../../interfaces/index'
import { FiFilter } from 'react-icons/fi';
import { PositionAPI } from '../../../interfaces/index';
import searchData from "../../../utils/search";
import { ActionTypes } from '../../../actions';
import LoadingV2, { LoadingType } from '../../../components/Loading/LoadingV2';
import { connect } from "react-redux";
import {
  Auth,
  loadAllPositions,
  setSelectedPosition,
  positionsStoreInterface,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { schoolLevelsStoreInterface } from '../../../actions/school_levels';
import { RiFileSearchLine } from 'react-icons/ri';
import { Link } from 'react-router-dom';

interface ApplicationPositionsProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI) => void;
  positions: positionsStoreInterface;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  setSelectedPosition: (position: PositionAPI) => void;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories : (callback: (loading: boolean) => void) => void;
  loadSchoolLevels : (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory : (education_category: Interfaces.education_category) => void;
  setSelectedSchoolLevel : (education_category: Interfaces.school_level) => void;
}

interface ApplicationPositionsState {
  schoolLevelsFilter: boolean;
  searchLevel: searchItem | null;
  filteredPositions: Interfaces.PositionAPI[] | null;
  searchPosition: string;
  showFilter: boolean;
  loading: boolean;
}

export interface searchItem {
  educationCategory: Interfaces.education_category | null;
  schoolLevel: Interfaces.school_level | null;
}

class _ApplicationPositions extends Component<ApplicationPositionsProps, ApplicationPositionsState> {
  constructor(props: ApplicationPositionsProps) {
		super(props);

		this.state = {
      schoolLevelsFilter: false,
      searchLevel: null,
      filteredPositions: null,
      searchPosition: "",
      showFilter: true,
      loading: false,
		};
	}
  setLoadingState = (state: boolean) => {
    this.setState({ loading: state });
  }

  setSchoolLevelFilterFn = (status:boolean) => {
    // this.setState({ schoolLevelsFilter: status });
    this.setState({ showFilter: status });
  }
  setSearchLevel = (educationCategory: Interfaces.education_category | null, schoolLevel: Interfaces.school_level | null) => {
    this.setState({ searchLevel: { educationCategory: educationCategory, schoolLevel: schoolLevel } });
    if (educationCategory) {
      this.props.setSelectedEducationCategory(educationCategory);
    }
    if (schoolLevel) {
      this.props.setSelectedSchoolLevel(schoolLevel);
    }
    this.filterPositions(this.props.positions.positions_list, { educationCategory: educationCategory, schoolLevel: schoolLevel });
  }

  filterPositions = (myDATA: PositionAPI[] | null ,searchItem: searchItem | null) => {
    let result: PositionAPI[] | null = myDATA;
    if (myDATA !== null) { 
      if (searchItem !== null) {
        if (searchItem.educationCategory !== null) {
          result = myDATA.filter(itm => itm.education_institute_id.education_institute_id === searchItem.educationCategory?.education_institute_id);
        }
        if (searchItem.schoolLevel !== null) {
          let selected = myDATA.filter(itm => itm.school_level_id.school_level_id === searchItem.schoolLevel?.school_level_id);
          if (this.state.searchLevel !== null) {
            if (this.state.searchLevel?.educationCategory !== null && this.state.searchLevel?.educationCategory !== undefined) {
              result = selected.filter(itm => itm.education_institute_id.education_institute_id === this.state.searchLevel?.educationCategory?.education_institute_id)
            }
          }
        }
      }
    }
    return result;
  }

  componentDidMount = () => {
    if (this.props.schoolLevels.education_categories === null || this.props.schoolLevels.education_categories.length === 0) {
      this.props.loadEducationCategories(this.setLoadingState);
    }
    if (this.props.schoolLevels.school_levels === null || this.props.schoolLevels.school_levels.length === 0) {
      this.props.loadSchoolLevels(this.setLoadingState);
    }
    if (this.props.positions.positions_list === null || this.props.positions.positions_list.length === 0) {
      this.props.loadAllPositions(this.setLoadingState);
    }
    this.setState({ searchLevel: { educationCategory: this.props.schoolLevels.selectedEducationCategory, schoolLevel: this.props.schoolLevels.selectedSchoolLevel } })
    if (this.props.schoolLevels.selectedEducationCategory !== null) {
      this.setState({ showFilter: false });
    }
    if (this.props.schoolLevels.selectedSchoolLevel !== null) {
      this.setState({ showFilter: false });
    }
  }

  render() {
    localStorage.removeItem(ActionTypes.ADD_ANSWER)
    return (
      <Fragment>
      <div className="w-full">
        <div className="grid md:grid-cols-12">
          <div className={`${this.state.schoolLevelsFilter === true ? "col-span-12" : "col-span-12"} h-full`}>
            <div className="">
              {this.props.positions.positions_list !== null && this.props.positions.positions_list.length > 0 &&
              <>
              <div className="flex flex-row justify-start items-center">
                <div onClick={() => this.setState({ showFilter: true })} className="rounded w-max px-3 py-2 bg-blue-100 text-blue-600 font-bold flex items-center justify-center cursor-pointer hover:bg-blue-100 border border-blue-600">
                  <FiFilter className="text-3xl md:text-2xl" />
                  <span className="hidden md:block">Filter by School levels</span>
                  <span className="block md:hidden">Filter</span>
                </div>
                <span className="text-lg my-0 md:my-2 mx-2 font-bold text-blue-600 uppercase">{this.state.searchLevel !== null && this.state.searchLevel.educationCategory?.education_category_title}{this.state.searchLevel?.schoolLevel !== null && this.state.searchLevel?.schoolLevel !== undefined && " - " + this.state.searchLevel.schoolLevel?.school_level_name}</span>
              </div>
              <div className="flex flex-row gap-1 mt-2">
                <input onChange={(e) => this.setState({ searchPosition: e.target.value })} value={this.state.searchPosition} type="text" placeholder="Type a position name" className="input-white border border-gray-400 h-10" />
              </div>
              </>}
              <div className="mt-6">
                <div className="flex flex-col gap-2">
                  {this.filterPositions(this.props.positions.positions_list, this.state.searchLevel) === null ? <LoadingV2 type={LoadingType.dark} /> : this.filterPositions(this.props.positions.positions_list, this.state.searchLevel)!.length === 0 ? 
                  <div className="py-5 px-3 text-gray-400 flex flex-col justify-center items-center">
                    <RiFileSearchLine className="text-9xl animate__animated animate__shakeX" />
                    <span className="text-2xl font-bold text-gray-500">No Application positions available!</span>
                    <Link to="/applications" className="btn bg-blue-100 text-blue-700 hover:bg-blue-200 border-2 border-white mt-6 hover:border-blue-700 hover:text-blue-900 animate__animated animate__zoomIn">View my applications</Link>
                  </div> : 
                  searchData(this.filterPositions(this.props.positions.positions_list, this.state.searchLevel)!, this.state.searchPosition).map((item, key) => (
                    <div key={key + 1} onClick={() => this.props.setCurrentPosition(item)} className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100">
                      <div className="grid lg:grid-cols-12">
                        <div className="col-span-12 lg:col-span-10">
                          <div className="flex flex-row items-center gap-3">
                            <div><IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" /></div>
                            <div className="flex flex-col">
                              <span className="text-xs lg:text-sm font-bold text-gray-700">{item.position_title}</span>
                              <span className="text-xs text-gray-500 italic">School level: <span className="text-gray-800 font-light" style={{fontStyle: 'normal'}}>{item.school_level_id.school_level_name}</span></span>
                              <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">{item.education_institute_id.education_category_title}</span>
                            </div>
                          </div>
                        </div>
                        <div className="col-span-12 lg:col-span-2 hidden lg:block">
                          <div className="h-full flex items-center justify-end">
                            <div className="btn bg-blue-600 text-white float-right">Choose</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {console.log("search: ", this.state.searchPosition)}
      {(this.state.showFilter === true && this.props.positions.positions_list !== null && this.props.positions.positions_list.length > 0) &&
      <Modal
          title={<div className="text-gray-500 font-bold text-2xl">Filter by school levels</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ showFilter: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          padding={{title: true,}} >
          <SchoolLevelsFilter 
            educationCategories={this.props.schoolLevels.education_categories}
            schoolLevels={this.props.schoolLevels.school_levels}
            searchLevel={this.state.searchLevel}
            setSearchLevel={this.setSearchLevel}
            setSchoolLevelFilterFn={this.setSchoolLevelFilterFn} />
        </Modal>
        }
      </Fragment>
    )
  }
}

const mapStateToProps = ({
  auth,
  positions,
  schoolLevels,
}: StoreState): { auth: Auth; positions: positionsStoreInterface; schoolLevels: schoolLevelsStoreInterface; } => {
  return { auth: auth, positions: positions, schoolLevels: schoolLevels };
};

const ApplicationPositions = connect(mapStateToProps, {
  loadAllPositions,
  setSelectedPosition,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
})(_ApplicationPositions);

export default ApplicationPositions;