import React, { Fragment, useEffect, useState } from "react";
import { IoDocumentOutline } from "react-icons/io5";
import { MdArrowBack, MdLabel, MdPlayArrow } from "react-icons/md";
import { RiEditBoxFill, RiFolderOpenFill } from "react-icons/ri";
import { connect } from "react-redux";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import {
  MainItem,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../components/Navigation/SideNavigation";
import Table from "../../components/Table/Table";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import UserDocuments from "./UserDocuments";
import {
  getDocumentCategory,
  getDocumentSubCategory,
} from "../../actions/document";
import {
  DistrictInterface,
  DocValidationStatusInterface,
  UserDocumentsInterface,
  UserInterface,
} from "../../shared/interfaces";
import {
  fetchUserAccess,
  verifyDocument,
  VerifyDocumentInterface,
} from "../../actions/users";
import axios from "axios";
import { API } from "../../utils/api";
import FileView from "./FileView";
import { errorToText } from "../../utils/errors";
import { Auth } from "../../actions";

interface AppProps {
  auth: Auth;
  userList: UserInterface[];
  fetchUserAccess: Function;
  verifyDocument: Function;
  documentCategories: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
}

export const countByStatus = (
  userDocs: UserDocumentsInterface[],
  docStatus: "waiting" | "approved" | "rejected"
): number => {
  let result: UserDocumentsInterface[] = userDocs;
  result = userDocs.filter(
    (details) => details.validation_status === docStatus && details.files
  );
  // if (docStatus === "waiting" || docStatus === "rejected") {
  //   result = userDocs.filter(
  //     (details) => details.validation_status === docStatus
  //   );
  // }
  // if (docStatus === "approved") {
  //   result = userDocs.filter(
  //     (details) => details.validation_status !== "waiting"
  //   );
  // }
  return result.length;
};

const DocumentAssment: React.FC<AppProps> = ({
  auth,
  userList,
  fetchUserAccess,
  verifyDocument,
  documentCategories,
  getDocumentCategory,
  getDocumentSubCategory,
}) => {
  const [candidateList, setCandidateList] = useState<UserInterface[]>();
  const [candidate, setCandidate] = useState<UserInterface | null>(null);
  const [doc, setDoc] = useState<UserDocumentsInterface | null>(null);
  const [docTitle, setDocTitle] = useState<string>("");
  const [responseMsg, setResponseMsg] = useState<string>("");
  const [response, setResponse] = useState<"approved" | "rejected">("approved");
  // const [docId, setDocId] = useState<string>("");
  // const [docFile, setDocFile] = useState<string>("");
  // const [docTitle, setDocTitle] = useState<string>("");
  // const [docStatus, setDocStatus] = useState<
  //   "waiting" | "approved" | "rejected"
  // >("waiting");
  // const [docSubTitle, setdocSubTitle] = useState<string>("");
  // const [docCategoryFields, setDocCategoryFields] =
  //   useState<DocumentFieldInterface[]>();
  const [loading, setLoading] = useState<boolean>(true);
  const [settingCandidates, setSettingCandidates] = useState<boolean>(true);
  const [loadingRequest, setLoadindRequest] = useState<boolean>(false);
  const [modalState, setModalState] = useState<boolean>(false);
  const [district, setDistrict] = useState<DistrictInterface[]>();

  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>({ type: "", message: "" });
  const [error, setError] = useState<{ element: string; msg: string }>({
    element: "",
    msg: "",
  });
  const [filterByCategory, setFilterByCategory] = useState<string>("");
  const [filterByCategoryId, setFilterByCategoryId] = useState<string>("");
  const [filterBySubCategory, setFilterBySubCategory] = useState<string>("");
  const [filterBySubCategoryId, setFilterBySubCategoryId] =
    useState<string>("");

  const countById = (
    candidates: UserInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ): number => {
    let result: UserInterface[] = candidates;
    let total: number = 0;

    if (filterType === "docCategory") {
      result = candidates.filter(
        (candidate) =>
          candidate.details.length > 0 &&
          candidate.details.filter(
            (details) =>
              details.category_id === filterValue &&
              details.files.length > 0 &&
              details.validation_status === "waiting"
          )
      );
      total = 0;
      for (var i = 0; i < result.length; i++) {
        for (var r = 0; r < result[i].details.length; r++) {
          total +=
            result[i].details[r].category_id === filterValue &&
            result[i].details[r].files.length > 0 &&
            result[i].details[r].validation_status === "waiting"
              ? 1
              : 0;
        }
      }
    } else if (filterType === "docSubCategory") {
      result = candidates.filter((candidate) =>
        candidate.details.filter(
          (details) =>
            details.sub_category_id === filterValue &&
            details.files.length > 0 &&
            details.validation_status === "waiting"
        )
      );

      total = 0;
      for (var k = 0; k < result.length; k++) {
        for (var j = 0; j < result[k].details.length; j++) {
          total +=
            result[k].details[j].sub_category_id === filterValue &&
            result[k].details[j].files.length > 0 &&
            result[k].details[j].validation_status === "waiting"
              ? 1
              : 0;
        }
      }
    }
    return total;
  };

  useEffect(() => {
    setLoading(true);
    setSettingCandidates(true);
    if (!userList.length) {
      fetchUserAccess();
    } else {
      console.log("setting candidates");
      let result: UserInterface[] = [];
      let action: boolean = true;
      for (let i = 0; i < userList.length; i++) {
        if (userList[i].details.length !== 0) {
          action = true;
          for (let j = 0; j < userList[i].details.length; j++) {
            if (
              userList[i].details[j].files.length > 0 &&
              userList[i].details[j].validation_status === "waiting" &&
              action
            ) {
              result.push(userList[i]);
              action = false;
            }
          }
        }
      }

      console.log({ result });
      setCandidateList(result);
      setSettingCandidates(false);
      setLoading(false);
    }

    if (!documentCategories.documentCategory.length) {
      getDocumentCategory();
    }
    if (!documentCategories.documentSubCategory.length) {
      getDocumentSubCategory();
    }

    if (!district) {
      loadDistrict();
    }
    console.log("loaded____");
  }, [
    userList,
    documentCategories.documentCategory.length,
    documentCategories.documentSubCategory.length,
    district,
    fetchUserAccess,
    getDocumentCategory,
    getDocumentSubCategory,
  ]);

  const loadDistrict = async () => {
    setLoading(true);
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      setDistrict(res.data);
      setLoading(false);
    } catch (error) {
      console.log({ code_error: error });
    }
  };

  // const countLabel = (
  //   data: UserInterface[],
  //   type: "waiting" | "assessed" | "rejected"
  // ): UserInterface[] => {
  //   let result: UserInterface[] = data.filter(
  //     (user) => user.details.length !== 0
  //   );
  //   if (type === "waiting" || type === "rejected") {
  //     result = result.filter((result) =>
  //       result.details.some((details) => details.validation_status === type)
  //     );
  //   }
  //   if (type === "assessed") {
  //     result = result.filter((result) =>
  //       result.details.every(
  //         (details) => details.validation_status !== "waiting"
  //       )
  //     );
  //   }

  //   console.log({ counte_results: result });
  //   return result;
  // };

  const filterData = (
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ) => {
    let result: UserInterface[];
    if (candidateList && candidateList.length !== 0) {
      result = candidateList;
    } else {
      let data: UserInterface[] = [];
      let action: boolean = true;
      for (let i = 0; i < userList.length; i++) {
        if (userList[i].details.length !== 0) {
          action = true;
          for (let j = 0; j < userList[i].details.length; j++) {
            if (
              userList[i].details[j].files.length > 0 &&
              userList[i].details[j].validation_status === "waiting" &&
              action
            ) {
              data.push(userList[i]);
              action = false;
            }
          }
        }
      }
      result = data;
    }

    if (filterType === "docCategory") {
      console.log("Filter category");

      // new idea
      let data: UserInterface[] = [];
      let action: boolean = true;
      for (let i = 0; i < userList.length; i++) {
        if (userList[i].details.length !== 0) {
          action = true;
          for (let j = 0; j < userList[i].details.length; j++) {
            if (
              userList[i].details[j].category_id === filterValue &&
              userList[i].details[j].files.length > 0 &&
              userList[i].details[j].validation_status === "waiting" &&
              action
            ) {
              data.push(userList[i]);
              action = false;
            }
          }
        }
      }
      result = data;
    }

    // filter by sub category new codes
    if (filterType === "docSubCategory") {
      console.log("Filter sub category");

      // new idea
      let data: UserInterface[] = [];
      let action: boolean = true;
      for (let i = 0; i < userList.length; i++) {
        if (userList[i].details.length !== 0) {
          action = true;
          for (let j = 0; j < userList[i].details.length; j++) {
            if (
              userList[i].details[j].sub_category_id === filterValue &&
              userList[i].details[j].files.length > 0 &&
              userList[i].details[j].validation_status === "waiting" &&
              action
            ) {
              data.push(userList[i]);
              action = false;
            }
          }
        }
      }
      result = data;
    }

    console.log({ filter: result });

    setCandidateList(result);
  };

  const tableData = (): JSX.Element[] => {
    if (settingCandidates) return [];
    if (!candidateList) return [];
    return candidateList.map((candidate, index) => (
      <tr key={candidate.user_id + index} className="border-b border-gray-200">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>
            {candidate.user_info.fname + " " + candidate.user_info.lname}
          </span>
        </td>
        <td className="p-3 text-left">
          {countByStatus(candidate.details, "approved")}
        </td>
        <td className="p-3 text-left">
          {countByStatus(candidate.details, "rejected")}
        </td>
        <td className="p-1 text-left">
          {countByStatus(candidate.details, "waiting") > 0 ? (
            <button
              className="flex items-center gap-3 border hover:border-2 border-white hover:border-yellow-800 hover:bg-yellow-100 rounded px-3 py-1 focus:outline-none"
              disabled={loadingRequest}
              onClick={() => {
                setCandidate(candidate);
                setModalState(true);
              }}
            >
              {countByStatus(candidate.details, "waiting")}
              {countByStatus(candidate.details, "waiting") > 0 && (
                <RiEditBoxFill className="text-yellow-800 font-bold w-6 h-6 focus:outline-none" />
              )}
            </button>
          ) : (
            <span className="px-3 py-1">0</span>
          )}
        </td>
        <td className="text-right">
          <button
            className="bg-blue-600 text-white font-bold py-1 px-1 inline-flex rounded focus:outline-none"
            title="View documents"
            disabled={loadingRequest}
            onClick={() => {
              setCandidate(candidate);
              setModalState(true);
            }}
          >
            <IoDocumentOutline className="w-5 h-5" />
            View docs
          </button>
        </td>
      </tr>
    ));
  };

  // const filterByProperty=(array,prop,value)=>{}

  const handleVerify = async () => {
    setLoadindRequest(true);
    if (candidate && doc && response) {
      if (response === "rejected" && !responseMsg.length) {
        setError({
          element: "comment",
          msg: "Reason of rejection is required",
        });
        return setLoadindRequest(false);
      }

      const api_body: VerifyDocumentInterface = {
        user_id: candidate.user_id,
        doc_id: doc.doc_id,
        validation_status:
          response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: auth.user.user_id,
          comment: responseMsg,
          validation_status:
            response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
        },
      };
      const api_redux: VerifyDocumentInterface = {
        user_id: candidate.user_id,
        doc_id: doc.doc_id,
        validation_status:
          response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: auth.user.user_id,
          comment: responseMsg,
          validation_status:
            response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
          date: "",
        },
      };
      try {
        const res = await axios.post<VerifyDocumentInterface>(
          `${API.AUTH}/validateuserdoc`,
          api_body
        );
        if (res) {
          console.log({ verify: res.data.validated_by });
          await verifyDocument(api_redux);
          setFeedBack({
            type: "success",
            message: `Document ${response} successful`,
          });
          setDoc(null);
          setResponseMsg("");
          setResponse("approved");
        }
      } catch (error) {
        console.error(error);
        setFeedBack({ type: "danger", message: errorToText(error) });
      }
      // setRejected(false);
      setLoadindRequest(false);
    }
  };

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading doc...
      </div>
    );
  }

  return (
    <Fragment>
      <SideNavigation>
        {/* <Search data={[1, 2, 3]} /> */}

        <span className="text-sm mt-2">Filter by document type</span>
        {documentCategories.documentCategory.map((category, index) => (
          <Fragment key={index + category.user_detail_category_id}>
            {candidateList &&
              countById(
                candidateList,
                "docCategory",
                category.user_detail_category_id
              ) > 0 && (
                <section key={index + category.user_detail_category_id}>
                  <section
                    className="mt-2"
                    onClick={() => {
                      setFilterByCategory(category.title);
                      setFilterByCategoryId(category.user_detail_category_id);
                      setFilterBySubCategory("");
                      setFilterBySubCategoryId("");
                      filterData(
                        "docCategory",
                        category.user_detail_category_id
                      );
                    }}
                  >
                    <MainItem
                      title={category.title}
                      count={
                        candidateList &&
                        countById(
                          candidateList,
                          "docCategory",
                          category.user_detail_category_id
                        )
                      }
                      active={category.title === filterByCategory}
                    />
                  </section>
                  {filterByCategory === category.title &&
                    documentCategories.documentSubCategory.filter(
                      (sub) =>
                        sub.category_id === category.user_detail_category_id
                    ).length > 0 && (
                      <SubItemContainer>
                        {documentCategories.documentSubCategory
                          .filter(
                            (sub) =>
                              sub.category_id ===
                              category.user_detail_category_id
                          )
                          .map(
                            (subCategory, i) =>
                              candidateList &&
                              countById(
                                candidateList,
                                "docSubCategory",
                                subCategory.user_detail_subcategory_id
                              ) > 0 && (
                                <section
                                  key={
                                    subCategory.user_detail_subcategory_id + i
                                  }
                                  onClick={() => {
                                    setFilterBySubCategory(subCategory.title);
                                    setFilterBySubCategoryId(
                                      subCategory.user_detail_subcategory_id
                                    );
                                    filterData(
                                      "docSubCategory",
                                      subCategory.user_detail_subcategory_id
                                    );
                                  }}
                                >
                                  <SubItem
                                    title={subCategory.title}
                                    icon={MdLabel}
                                    // count={
                                    //   candidateList
                                    //     ? countById(
                                    //         candidateList,
                                    //         "docSubCategory",
                                    //         subCategory.user_detail_subcategory_id
                                    //       )
                                    //     : 0
                                    // }
                                    active={
                                      subCategory.title === filterBySubCategory
                                    }
                                  />
                                </section>
                              )
                          )}
                      </SubItemContainer>
                    )}
                </section>
              )}
          </Fragment>
        ))}
      </SideNavigation>

      {/* Title */}
      <div className="w-full my-5">
        {/* header title */}
        <div className="flex flex-row items-center gap-2">
          <RiFolderOpenFill className="text-blue-700 w-24 h-24" />
          <div className="flex justify-between w-full">
            <div className="flex flex-col">
              <h1 className="text-3xl font-semibold">
                Application documents checking
              </h1>

              <div className="flex flex-row items-center md:gap-3 gap-2">
                <div className="flex flex-row items-center mt-1 gap-1">
                  <span className="text-gray-600 text-lg">level:</span>
                  <span className="text-gray-600 text-lg font-bold">
                    Country
                  </span>
                  {/* <MdKeyboardArrowRight />
                  <span className="text-blue-600 text-lg font-bold">
                    Room Alpha
                  </span> */}
                </div>
              </div>
            </div>
            {/* <div className="text-right flex flex-col">
              <span className="text-gray-700">Nyarugenge District</span>
              <span className="text-gray-700">Center of Nyakabanda</span>
            </div> */}
          </div>
        </div>
        {/* end header title */}

        <div className="bg-white shadow-md p-2 overflow-x-auto">
          {filterByCategory && (
            <div className="inline-flex items-center px-1 gap-1">
              <span
                className={`text-blue-600 text-base font-semibold ${
                  filterBySubCategory && "cursor-pointer"
                }`}
                onClick={() =>
                  filterBySubCategory && setFilterBySubCategory("")
                }
              >
                {filterByCategory} (
                {candidateList
                  ? countById(candidateList, "docCategory", filterByCategoryId)
                  : 0}
                )
              </span>
              {filterBySubCategory && (
                <div className="inline-flex items-center gap-1">
                  <MdPlayArrow className="w-5 h-5" />
                  <span className="text-gray-500 font-semibold underline">
                    {filterBySubCategory} (
                    {candidateList
                      ? countById(
                          candidateList,
                          "docSubCategory",
                          filterBySubCategoryId
                        )
                      : 0}
                    )
                  </span>
                </div>
              )}
            </div>
          )}
          {
            <Table
              columns={[
                { columnTitle: "#", alignText: "left", padding: "p-3" },
                { columnTitle: "Names", alignText: "left", padding: "p-3" },
                { columnTitle: "Approved", alignText: "left", padding: "p-3" },
                { columnTitle: "Rejected", alignText: "left", padding: "p-3" },
                { columnTitle: "Pending", alignText: "left", padding: "p-3" },
                { columnTitle: "", alignText: "left", padding: "p-3" },
              ]}
              rows={tableData}
              pagination={true}
              search={true}
              thColor="gray"
            />
          }
        </div>
      </div>

      {/* modal control */}
      {modalState && candidate && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            doc ? setDoc(null) : setModalState(false);
            setFeedBack({ type: "", message: "" });
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-600 font-bold text-3xl md:ml-4 inline-flex gap-2">
              {doc && (
                <button
                  className="p-0 w-10 h-10 bg-gray-100 border-2 border-gray-500 rounded-full hover:bg-gray-500 hover:text-gray-50 active:shadow-lg shadow transition ease-in duration-200 focus:outline-none"
                  onClick={() => setDoc(null)}
                >
                  <MdArrowBack className="w-6 h-6 inline-block" />
                </button>
              )}
              {candidate.user_info.fname + " " + candidate.user_info.lname}'s
              document checking
            </div>
          }
          widthSizeClass={ModalSize.maxWidth}
          padding={{ title: true, body: false, footer: false }}
        >
          {doc === null ? (
            <UserDocuments
              candidate={candidate}
              setDoc={setDoc}
              setDocTitle={setDocTitle}
              feedBack={feedBack}
              setFeedBack={setFeedBack}
            />
          ) : (
            <FileView
              doc={doc}
              setResponseMsg={setResponseMsg}
              responseMsg={responseMsg}
              loadingRequest={loadingRequest}
              setResponse={setResponse}
              handleVerify={handleVerify}
              docTitle={docTitle}
              error={error}
              setError={setError}
            />
          )}
        </Modal>
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  users,
  document,
}: StoreState): {
  auth: Auth;
  userList: UserInterface[];
  documentCategories: DocumentReducerInterface;
} => {
  return { auth: auth, userList: users, documentCategories: document };
};

export default connect(mapStateToProps, {
  fetchUserAccess,
  verifyDocument,
  getDocumentCategory,
  getDocumentSubCategory,
})(DocumentAssment);
