import axios from "axios";
import React, { useState } from "react";
import { MdClear, MdCreate } from "react-icons/md";
import { connect } from "react-redux";
import { deleteDocumentSubCategory } from "../../actions/document";
import { DocumentSubCategoryInterface } from "../../shared/interfaces";
import { API } from "../../utils/api";

interface AppProps {
  categoryId: string;
  categoryTitle: string;
  setSubCategoryId: React.Dispatch<React.SetStateAction<string>>;
  setSubCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  setSubCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  setActionType: React.Dispatch<
    React.SetStateAction<
      "" | "CREATE_SUB" | "CREATE_CAT" | "UPDATE_SUB" | "UPDATE_CAT"
    >
  >;
  actionType: string;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  deleteDocumentSubCategory: Function;
  documentSubCategory: DocumentSubCategoryInterface[];
}

const SubCategory: React.FC<AppProps> = ({
  categoryId,
  categoryTitle,
  actionType,
  setActionType,
  setSubCategoryId,
  setSubCategoryTitle,
  setSubCategoryDescription,
  setFeedBack,
  documentSubCategory,
  deleteDocumentSubCategory,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const sub_cats = documentSubCategory.filter(
    (sub) => sub.category_id === categoryId
  );
  const handleDelete = async (subCategoryId: string) => {
    setFeedBack({ type: "", message: "" });
    setLoadind(true);
    try {
      const res = await axios.delete<DocumentSubCategoryInterface>(
        `${API.AUTH}/detailsubcategory/${subCategoryId}`
      );
      if (res.data) {
        deleteDocumentSubCategory(subCategoryId);
        setFeedBack({
          type: "success",
          message: `Sub Category has been deleted successfully.`,
        });
        setLoadind(false);
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      setLoadind(false);
    }
  };

  return (
    <div>
      {/* user details about */}
      <div className="border-b px-2 py-3 flex justify-between">
        <div className="flex items-center">
          Category:
          <h2 className="mx-3 text-xl font-semibold text-gray-800">
            {categoryTitle}
          </h2>
        </div>
        <button
          type="button"
          className="bg-gray-300 text-gray-700 px-4 py-2 duration-500 ease select-none hover:bg-gray-400 hover:text-black focus:outline-none focus:shadow-outline"
          onClick={() => setActionType("CREATE_SUB")}
          disabled={actionType === "CREATE_SUB" || actionType === "UPDATE_SUB"}
        >
          + Sub Category
        </button>
      </div>
      {/* {feedBack && actionType === "" && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )} */}
      {sub_cats.length ? (
        sub_cats.map((sub_category, i) => (
          <div
            key={i}
            className="flex justify-between items-center p-2 my-3 rounded-lg border border-gray-100 shadow-md"
          >
            <div className="flex items-center">
              <div className="ml-2">
                <div className="text-xl font-semibold text-gray-600">
                  {sub_category.title}
                </div>
                <div className="text-sm font-light text-gray-500">
                  {sub_category.description}
                </div>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <button
                className="bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center"
                onClick={() => {
                  setSubCategoryId(sub_category.user_detail_subcategory_id);
                  setSubCategoryTitle(sub_category.title);
                  setSubCategoryDescription(sub_category.description);
                  setActionType("UPDATE_SUB");
                }}
              >
                <MdCreate className="text-white w-4 h-4" />
              </button>
              <button
                className="bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center"
                onClick={() =>
                  handleDelete(sub_category.user_detail_subcategory_id)
                }
                disabled={loading}
              >
                <MdClear className="text-white w-4 h-4" />
              </button>
            </div>
          </div>
        ))
      ) : (
        <div className="text-center my-4 text-gray-500">
          No Sub Categories Available
        </div>
      )}
      {/* <!-- End of profile tab --> */}
    </div>
  );
};

export default connect(null, { deleteDocumentSubCategory })(SubCategory);
