import axios from "axios";
import React, { useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentFieldInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentCategory,
  updateDocumentCategory,
} from "../../actions/document";
import { MdClose, MdErrorOutline } from "react-icons/md";

interface AppProps {
  setCategoryId: React.Dispatch<React.SetStateAction<string>>;
  categoryId: string;
  setCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  categoryTitle: string;
  setCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  categoryDescription: string;
  setActionType: React.Dispatch<
    React.SetStateAction<
      "" | "CREATE_SUB" | "CREATE_CAT" | "UPDATE_SUB" | "UPDATE_CAT"
    >
  >;
  actionType: string;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  createDocumentCategory: Function;
  updateDocumentCategory: Function;
  categoryFields: DocumentFieldInterface[];
  setCategoryFields: React.Dispatch<
    React.SetStateAction<DocumentFieldInterface[]>
  >;
}
const CreateDocument: React.FC<AppProps> = ({
  setCategoryId,
  categoryId,
  setCategoryTitle,
  categoryTitle,
  setCategoryDescription,
  categoryDescription,
  categoryFields,
  setCategoryFields,
  setActionType,
  actionType,
  setFeedBack,
  feedBack,
  createDocumentCategory,
  updateDocumentCategory,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);

  const updateCategoryValue = (
    value: DocumentFieldInterface,
    isAvailable: boolean
  ) => {
    // Destructure current state array
    const arrayvalue: DocumentFieldInterface[] = [...categoryFields];
    if (isAvailable) {
      var index = arrayvalue.indexOf(value);
      if (index !== -1) {
        arrayvalue.splice(index, 1);
      }
    } else {
      arrayvalue.push(value);
    }
    setCategoryFields(arrayvalue);
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });
    if (!categoryFields.length) {
      setFeedBack({
        type: "warning",
        message: "You must select at least one field",
      });
      return;
    }
    setLoadind(true);
    try {
      if (actionType === "CREATE_CAT") {
        const res = await axios.post<DocumentCategoryInterface>(
          `${API.AUTH}/detailcategory`,
          {
            title: categoryTitle,
            description: categoryDescription,
            fields: categoryFields,
          }
        );
        createDocumentCategory(res.data);
        setFeedBack({
          type: "success",
          message: `Category has been created!`,
        });
        setLoadind(false);
        setCategoryTitle("");
        setCategoryDescription("");
        setCategoryFields([]);
      } else {
        const formData: DocumentCategoryInterface = {
          user_detail_category_id: categoryId,
          title: categoryTitle,
          description: categoryDescription,
          fields: categoryFields,
        };
        const res = await axios.patch<DocumentCategoryInterface>(
          `${API.AUTH}/detailcategory`,
          formData
        );
        if (res.data) {
          updateDocumentCategory(formData);
          setFeedBack({
            type: "success",
            message: `Category has been updated!`,
          });
          setCategoryId("");
          setCategoryTitle("");
          setCategoryDescription("");
          setCategoryFields([]);
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      setLoadind(false);
    }
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="divide-y divide-gray-200">
        <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
          <div className="flex flex-col">
            <label>Title</label>
            {console.log({ categoryFields })}
            <input
              type="text"
              className="input input-border"
              placeholder="Sub Category Title"
              value={categoryTitle}
              onChange={(e) => setCategoryTitle(e.target.value)}
              required
            />
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className="input input-border"
              placeholder="Category Description"
              style={{ resize: "none" }}
              value={categoryDescription}
              onChange={(e) => setCategoryDescription(e.target.value)}
              rows={2}
              required
            ></textarea>
          </div>
          <div className="flex flex-col">
            <label>Fileds</label>
            <span className="text-gray-500 text-sm">
              Required Fields for this document
            </span>
            <div className="grid grid-flow-row grid-cols-3 grid-rows- gap-4">
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.Title,
                      categoryFields.includes(DocumentFieldInterface.Title)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.Title)
                  }
                />
                <span className="ml-2 text-gray-700">Title</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.Description,
                      categoryFields.includes(
                        DocumentFieldInterface.Description
                      )
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.Description)
                  }
                />
                <span className="ml-2 text-gray-700">Description</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.Files,
                      categoryFields.includes(DocumentFieldInterface.Files)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.Files)
                  }
                />
                <span className="ml-2 text-gray-700">Files</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.email,
                      categoryFields.includes(DocumentFieldInterface.email)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.email)
                  }
                />
                <span className="ml-2 text-gray-700">Email</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.names,
                      categoryFields.includes(DocumentFieldInterface.names)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.names)
                  }
                />
                <span className="ml-2 text-gray-700">Names</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.phone_number,
                      categoryFields.includes(
                        DocumentFieldInterface.phone_number
                      )
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.phone_number)
                  }
                />
                <span className="ml-2 text-gray-700">Phone</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.position,
                      categoryFields.includes(DocumentFieldInterface.position)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.position)
                  }
                />
                <span className="ml-2 text-gray-700">Position</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.relation,
                      categoryFields.includes(DocumentFieldInterface.relation)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.relation)
                  }
                />
                <span className="ml-2 text-gray-700">Relation</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.src,
                      categoryFields.includes(DocumentFieldInterface.src)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.src)
                  }
                />
                <span className="ml-2 text-gray-700">SRC</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.year,
                      categoryFields.includes(DocumentFieldInterface.year)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.year)
                  }
                />
                <span className="ml-2 text-gray-700">Year</span>
              </label>
              <label className="inline-flex items-center mt-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-5 w-5 text-gray-600"
                  onChange={() =>
                    updateCategoryValue(
                      DocumentFieldInterface.company,
                      categoryFields.includes(DocumentFieldInterface.company)
                    )
                  }
                  checked={
                    categoryFields &&
                    categoryFields.includes(DocumentFieldInterface.company)
                  }
                />
                <span className="ml-2 text-gray-700">Company</span>
              </label>
            </div>
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setCategoryTitle("");
              setCategoryDescription("");
              setCategoryFields([]);
            }}
            className="btn bg-gray-400 text-white hover:bg-gray-600"
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className="btn bg-primary text-white hover:bg-blue-600"
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_CAT"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default connect(null, {
  createDocumentCategory,
  updateDocumentCategory,
})(CreateDocument);
