import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { courseStoreInterface } from '../../../../actions/course';
import { schoolLevelsStoreInterface } from '../../../../actions/school_levels';
import { PopupAlertData } from '../../../../components/Alert/Popup';
import { formErrorInterface } from '../../../../interfaces/form';
import { PopupAlertTheme } from '../../../../interfaces/popupAlert';
import { API } from '../../../../utils/api';
import SchoolLevelSelect from '../SettingsComponents/SchoolLevelSelect';
import { CoursesAction, S_CourseDetails } from './Courses';

interface AddCourseProps {
  closeModal: (state: boolean) => void;
  courses: courseStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  selectedCourse: S_CourseDetails | null;
  deleteSelected: (selectedDetail: S_CourseDetails) => void;
  schoolLevels: schoolLevelsStoreInterface;
}

const AddCourse = (props: AddCourseProps) => {
  // States
  const [course_id, setCourse_id] = useState<string>("");
  const [course_name, setCourse_name] = useState<string>("");
  const [course_code, setCourse_code] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [school_levels, setSchool_levels] = useState<string[]>([]);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const submitCourse = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (props.selectedCourse?.action === CoursesAction.Update && course_id === "") {
      return setFormError({
        element: "course_id",
        msg: "Please choose Course"
      });
    }
    else if (course_name === "") {
      return setFormError({
        element: "course_name",
        msg: "Please fill the course name"
      });
    }
    else if (course_code === "") {
      return setFormError({
        element: "course_code",
        msg: "Please fill the course code"
      });
    }
    else if (description === "") {
      return setFormError({
        element: "description",
        msg: "Please fill the description"
      });
    } else {
      setLoading(true);
      try {
        // Update current
        if (props.selectedCourse !== null && props.selectedCourse.selectedCourse.course_id !== undefined) {
          const res = await axios.patch(`${API.APPLICATION}/course`, {
            course_id: course_id,
            course_name: course_name,
            course_code: course_code,
            description: description,
            school_levels: school_levels,
          });
          props.PushPopupAlert({
            title: `Course updated successfully`,
            description: `After updating the course, you have access to delete and update it!`,
            theme: PopupAlertTheme.info,
          });
          props.closeModal(false);
          console.log("Result: ", res);
        } else {
          // Add new
          const res = await axios.post(`${API.APPLICATION}/course`, {
            course_name: course_name,
            course_code: course_code,
            description: description,
            school_levels: school_levels,
          });
          props.PushPopupAlert({
            title: `Course added successfully`,
            description: `After adding the Course, you have access to delete and update it!`,
            theme: PopupAlertTheme.success,
          });
          props.closeModal(false);
          console.log("Result: ", res);
        }
      } catch (error) {
        props.PushPopupAlert({
          title: `Failed to add course! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.log("Err: ", error);
      }
      setLoading(false);
    }
  }

  useEffect(() => {
    if (props.selectedCourse !== null) {
      console.log("Sle: ", props.selectedCourse);
      if (course_id === "") {
        setCourse_id(props.selectedCourse.selectedCourse.course_id);
      }
      if (course_name === "") {
        setCourse_name(props.selectedCourse.selectedCourse.course_name);
      }
      if (course_code === "") {
        setCourse_code(props.selectedCourse.selectedCourse.course_code);
      }
      if (description === "") {
        setDescription(props.selectedCourse.selectedCourse.description);
      }
      if (school_levels.length === 0) {
        setSchool_levels(props.selectedCourse.selectedCourse.school_levels);
      }
    }
  }, [props, course_id, course_name, course_code, description, school_levels])
  return (
    <form onSubmit={submitCourse} className="-mt-2">
      <div className="grid md:grid-cols-12 gap-5">
        <div className="col-span-6">
          <SchoolLevelSelect
            schoolLevels={props.schoolLevels}
            school_levels={school_levels}
            setSchool_levels={setSchool_levels}
          />
        </div>
        <div className="col-span-6">
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mb-1">Course name</span>
            <input 
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                setCourse_name(e.target.value);
                setFormError(null);
              }}
            type="text" value={course_name} 
            className={`input-white border-2 ${formError !== null && formError.element === "course_name" ? "border-red-500" : "border-gray-500"}`} />
            {formError !== null && formError.element === "course_name" &&
            <span className="text-red-600 font-bold text-xs">{formError.msg}</span>}
          </div>
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mb-1">Course code</span>
            <input 
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                setCourse_code(e.target.value);
                setFormError(null);
              }}
            type="text" value={course_code} 
            className={`input-white border-2 ${formError !== null && formError.element === "course_code" ? "border-red-500" : "border-gray-500"}`} />
            {formError !== null && formError.element === "course_code" &&
            <span className="text-red-600 font-bold text-xs">{formError.msg}</span>}
          </div>
          <div className="flex flex-col mb-2">
            <span className="text-sm text-gray-600 font-bold mt-2 mb-1">Description</span>
            <textarea 
              disabled={loading}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                setDescription(e.target.value);
                setFormError(null);
              }}
              value={description}
              className={`input-white border-2 ${formError !== null && formError.element === "description" ? "border-red-500" : "border-gray-500"}`}
              style={{minHeight: '100px'}}>
              {description}
            </textarea>
            {formError !== null && formError.element === "description" &&
            <span className="text-red-600 font-bold text-xs">{formError.msg}</span>}
          </div>
          <div className="flex flex-row justify-between items-center">
            {props.selectedCourse?.action === CoursesAction.View ? 
            <button onClick={() => 
            props.selectedCourse && 
            props.deleteSelected({ 
              action: CoursesAction.Delete,
              selectedCourse: props.selectedCourse.selectedCourse
            })} type="button" className="btn border border-red-600 text-red-700">Delete</button> : <div></div>}
            <button 
            disabled={loading}
            type="submit" className={`btn bg-blue-600 text-white ${loading === true ? "animate__animated animate__flash animate__infinite" : ""}`}>
              {loading === true ? "Please wait..." : props.selectedCourse !== null ? "Update course" : "Add course"}
            </button>
          </div>
        </div>
      </div>
    </form>
  )
}

export default AddCourse
